body {
    padding: 0;
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;  /* this is because images get stretched in flexbox */
}

.nav-logo {
    width: 50px;
    padding-left: 0.3em;
}

.nav-items {
    list-style: none;
    display: flex;
}

.nav-items > li {
    padding: 0.6em;
}

.nav-underline {
    height: 5px; 
    background: linear-gradient(90deg, #14213D 0%, #fca311 100%);
}

